<div class="chats" v-bind:class="{ full : isFull, mini: isMini }"><div class="ui inverted big menu" id="menu"><router-link class="item relative overflow-visible" to="/feedback"><img class="abs-upper-right" src="../assets/new-yellow.png"><i class="comment icon no-float"></i>{{ $t('chat.feedback') }}</router-link><div class="item ui form" v-show="isFull"><div class="ui input"><input v-model="key" placeholder="搜尋"></div></div><div class="right menu"><a class="item" v-if="!isFull" @click="isFull = true; isMini = false; reCount()"><i class="comments icon"></i>{{$t('chat.gc')}}<span class="red" v-show="fil(chats).length &gt; read">({{ fil(chats).length - read }})    </span></a><a class="item" v-if="!isMini" @click="isFull = false; isMini = true"><i class="compress icon"></i>{{$t('chat.cp')}}</a></div></div><div id="box"><div class="ui list"><div class="item" v-for="(c, idx) in fil(chats).slice(fil(chats).length - 5, fil(chats).length)" v-bind:key="c.t"><p v-show="edit !== c"> <router-link class="fat-only" :to="'/flag/' + c.uid"><img class="ui avatar" :src="c.photoURL || 'http://graph.facebook.com/' + c.uid + '/picture'" alt="^_^"></router-link><a class="fat-only" @click="key = c.l" v-bind:class="c.l">[{{c.l}}]</a><span class="text">{{ c.n }} : {{ c.t }}</span><span class="gray fat-only" v-show="isFull">&nbsp;&nbsp;-{{ countDateDiff(c.time) }}</span></p></div><div class="item" v-if="uid"><div class="ui form"><div class="field"><img class="ui avatar" :src="photoURL"><input class="input" v-model="msg" placeholder="在想什麼嗎?" autofocus></div><div class="inline fields"><div class="field" v-for="l in labels"><div class="ui radio checkbox"><input type="radio" name="l" v-model="label" :value="l"><label><a @click="label=l" v-bind:class="l">{{ l }}</a></label></div></div><div class="ui button group"><a class="ui green small button" @click="submitChat">{{ $t('chat.submit') }}</a></div></div></div></div><div class="item" v-else><div class="ui big buttons"><button class="ui orange button" @click="toggleLogin"><!-- i.google.icon-->登入以留言</button></div></div></div></div></div>