<div class="ui dimmer modals visible active clickable" id="login-bg" style="background: rgba(0, 0, 0, 0.6);" @click="toggleLogin()"><div class="ui middle aligned center aligned grid" id="login-main" style="height: 100vh;"><div class="column" style="max-width: 350px;"><div class="ui raised segment"><h2 class="ui black header" style="font-size: 1.5rem; font-weight: 600;">{{$t('login.login_into_your_account')}}</h2><div class="ui fluid large button" @click.stop="loginGoogle" style="background-color: #4285F4 !important; color: white;"><i class="google icon"></i>{{$t('login.login_with_google')}}</div><div class="ui horizontal divider" style="color: #ccc;">{{$t('login.or')}}</div><div class="ui form segment" style="border-radius: 10px; padding: 15px; background-color: #f0f0f0;"><div class="field"><div class="ui left icon input"><i class="envelope icon"></i><input type="text" name="email" placeholder="E-mail address" style="font-size: 14px;" v-model="users_email" @click.stop></div></div><div class="field"><div class="ui left icon input"><i class="lock icon"></i><input type="password" name="user_password" placeholder="Password" style="font-size: 14px;" v-model="user_password" @click.stop></div></div><div class="ui fluid large button" @click.prevent="loginWithEmail" style="background-color: #e47e10 ; color: white; font-weight: bold;">登入</div><a class="small forgot-password" @click="resetPassword">{{$t('login.forgot_password')}}</a></div><form class="ui large form"><div class="ui segment" style="border: none; padding-top: 10px;"><div class="field"><div class="ui checkbox" @click.stop><input type="checkbox" v-model="keeploggedin"><label>{{$t('login.keep_me_logged_in')}}</label></div></div></div><p>新用戶？按此<div class="ui large basic button" id="register-btn" @click.stop="registerWithEmail">註冊</div></p></form></div></div></div></div>